@import url("https://fonts.googleapis.com/css2?family=Inter:wght@100;200;300;400;500;600;700;800;900&display=swap");

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;

}

body {
    /* background-color: rgb(248, 246, 253); */
    background-image: url(../../assets/globe.png);


}

body,
input,
textarea,
button {
    font-family: "Inter", sans-serif;
}

.form {

    height: 100vh;
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-color: #ffff;
}

.form>h1 {
    margin-bottom: 30px;
}

.form>input,
textarea,select {
    padding: 20px;
    border-radius: 10px;
    /* box-shadow: 0px 0px 5px rgba(0, 0, 0, 0.137); */
    margin-bottom: 20px;
    /* border: 1px solid lightgray; */
    border: none;
    background: #fff;
    font-size: 16px;
    color: rgb(0, 0, 32);
    outline: none;
    width: 500px;
    border-radius: 10px;

}

.form>label {
    padding-bottom: 10px;
    color: rgb(0, 0, 32);
    font-weight: bold;
}

/* Upload file styling */
.file-card {
    padding: 1em;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    border: 3px dashed #cbd5e0;
    background-color: #edf2f7;
    min-width: 380px;
    min-height: 230px;
}

.file-inputs {
    position: relative;
    margin-bottom: 1.5em;

}

.file-inputs > input {
    position: relative;
    text-align: right;
    opacity: 0;
    z-index: 2;
    cursor: pointer;
    height: 46px;
    max-width: 200px;
}
button {
    /* position: absolute;
    top: 0px; */
    margin-top: 50px;
    left: 0px;
    width: 200px;
    height: 70px;
    z-index: 1;
    display: flex;
    justify-content: center;
    align-items: center;

    color: #fff;
    background-color: #f266b3;
    font-size: 1.1rem;
    cursor: pointer;
    border-radius: 4px;
    border: none;
    outline: none;
    /* padding: 1em; */
    transition: background-color 0.4s;
    box-shadow: 0px 8px 24px rgba(149, 157, 165, 0.5);
}

button > i {
    width: 1.5em;
    height: 1.5em;
    padding: 0.4em;
    background-color: #fff;
    color: #f266b3;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-right: 0.8em;
    font-size: 0.8em;
}

.main, .info, .row {
    color: #f266b3;
}






